.chunk-upload-holder {

  .chunk-errors {

    color: red;

    ul {
      padding: 0;
      margin: 0;
    }
  }

}


.chunk-filelist {

  ul {
    padding: 0;
    margin: 0;
    display: block;
    list-style: none;
    list-style-type: none;
  }

  li {
    padding: 10px 40px 10px 10px;
    position: relative;

    .delete {
      position: absolute;
      right: 0;
      top: 50%;
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      -o-transform: translateY(-50%);
      transform: translateY(-50%);
    }
  }

}

.chunk-upload-field {

    border: 1px solid #0ea0eb;
    background: #2B6FB6;
    position: relative;

    &:before {
      content: " ";
      display: block;
      width: 100%;
      padding-bottom: 80px;
    }

    .chunk-droppable {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    button {
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      -o-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      z-index: 3;
      margin: 0;
    }

    .chunk-progress {
      position: absolute;
      bottom: 0;
      left: 0;
      height: 5px;
      background: #00ff00;
    }

}